.app {
    width: 70%;
    min-height: 850px;
    background: #FFFFFF;
    box-shadow: 0 0  15px 20px rgba(0, 0, 0, 0.2);
    border-radius: 10px;
    padding: 45px 0;
    margin: 50px auto;
}

@media (max-width: 1000px) {
    .app {
        width: 90%;
    }
}

@media (max-width: 600px) {
    .app {
        margin-top: 10px;
        margin-bottom: 5px;
        padding: 45px 0;
        width: 98%;
    }
}